.beauty-link {

    .firstscreen {

        &__subtitle {
            margin-bottom: 0;
        } //.firstscreen__subtitle

        &__inner {
            display: flex;
            justify-content: center;
            padding: 0 15px;
            padding-bottom: 75%;

            @media (min-height: 811px) and (max-width: 375px) {
                padding-bottom: 95%;
            }

            @include respond-min(sm) {
                padding-bottom: 58%;
            }

            @include respond-min(md) {
                justify-content: flex-start;
                padding-bottom: 0;
            }
        } //.firstscreen__inner

        &__wrapper {
            position: relative;
        } //.firstscreen__wrapper

        &__btn {
            position: absolute;
            bottom: 30px;

            @include respond-min(md) {
                position: relative;
                bottom: 0;
            }
        } //.firstscreen__btn
    } //.firstscreen

    .screen {

        &__img {
            max-width: 450px;

            @include respond-min(md) {
                max-width: 355px;
            }
        } //.screen__img

        &--img {
            height: 600px;
            overflow: hidden;
        } //.screen--img
    } //.screen

    .icon-list {

        &__title {

            @include respond-min(md) {
                font-size: 25px;
            }
        } //.icon-list__title

        &__item {
            align-items: flex-start;

            @include respond-min(sm) {
                align-items: center;
            }
        } //.icon-list_item

        &__icon {
            width: 60px;

            @include respond-min(md) {
                width: 70px;
            }

            img {
                width: 50px;
                height: 50px;

                @include respond-min(md) {
                    width: 70px;
                    height: 70px;
                }
            }

            &--all-messengers {

                img {
                    height: 60px;
                }
            } //.icon-list__all-messengers
        } //.icon-list__icon
    } //.icon-list

    .modal {

        &-dialog {
            @include flex;
            justify-content: center;
        } //.modal-dialo

        &-content {
            max-width: 450px;
        } //.modal-content

        &-body {
            position: relative;
            @include flexColumn;
            justify-content: center;
        } //.modal-body

        &-close {
            position: absolute;
            top: 25px;
            right: 12px;
            border: 0;
            background: transparent;
            line-height: 0;

            i {
                line-height: 0;
                font-size: 25px;
            }
        } //.modal-close
    } //.modal

    .bb-slider {

        &--fullscreen-slider {

            .slick {

                &-dots {
                    bottom: 40%;

                    @include respond-min(md) {
                        bottom: 125px;
                    }

                    li {
                        margin: 0 3px;

                        button::before {
                            width: 16px;
                            height: 16px;
                            transform: scale(1);
                            background-color: white;
                        }
                    }
                } //.slick-dots
            } //.slick
        } //.bb-slider--fullscreen-slider
    } //.bb-slider

    .fullscreen-slider {

        &__video {

            video {
                position: absolute;
                top: 0;
                left: -540px;
                height: 100%;

                @media (min-height: 811px) and (max-width: 375px) {
                    left: -700px;
                }

                @include respond-min(i7) {
                    left: -650px;
                }

                @include respond-min(sm) {
                    left: -200px
                }

                @include respond-min(md) {
                    left:-19px;
                }
            }
        } //.fullscreen-slider__video
    } //.fullscreen-slider
} //.beauty-link

.fullscreen-slider {

    &__video {
        position: absolute;
        width: 100%;
        overflow: hidden;
    } //.fullscreen-slider__video

    .slick {

        &-track {
            height: 100%;
        } //.slick-track

        &-list {
            height: 100%;
        } //.slick-list

        &-slide {
            margin: 0;

            div {
                height: 100%;
            }
        } //.slick-slide
    } //.slick
} //.fullscreen-slider
